<script>
import popup from './components/popup'
export default {
    name:'Common',
    components: {
        popup
    },
    data(){
        return{
            //本部门人员
            memberList:null,
            //所有人员
            list:null,
            //搜索框内容
            keyword:null,
            //选中人员的值
            radvalue:[],
            isPopupVisible: false
        }
    },
    mounted() {
        this.initpage()
    },
    methods:{
        showPopup(){
            this.isPopupVisible = true
        },
        closePopup(){
            this.isPopupVisible = false
        },

        //根据名称获取人员信息
        getNameInfo(){
            if(this.keyword == ''){
                this.initpage()
            }else{
                var getname = encodeURI(this.keyword);
                var getname2 = this.keyword;
                console.log(getname);
                var token = this.getToken("rest", "123456");
                var url = "http://10.1.19.170";
                this.memberList = new Array();
                var _this =this
                $.ajax({
                    url: (url + '/seeyon/rest/orgMembers/name/'+getname+'?token='+token),
                    type: "GET",
                    dataType: "text",
                    async: false,
                    processData: false,
                    success: function (data) {
                        if(data!="[ ]"){
                            var orgLogin = data.replace(/(\".*?\"\s*\:\s*)(\-{0,1}\d+)/g, '$1' + '"$2"');
                            var dateLogin = eval('(' + orgLogin + ')');
                            _this.memberList = dateLogin
                            console.log(_this.memberList)
                        }

                    },
                    error: function (XMLHttpRequest, textStatus, errorThrown) {
                        console.log("获取名称失败！");
                    },
                });

                $.ajax({
                    url: (url + '/seeyon/rest/orgMember/code/'+getname2+'?token='+token),
                    type: "GET",
                    dataType: "text",
                    async: false,
                    processData: false,
                    success: function (data) {
                        if(data!="[ ]"){
                            var orgLogin = data.replace(/(\".*?\"\s*\:\s*)(\-{0,1}\d+)/g, '$1' + '"$2"');
                            var dateLogin = eval('(' + orgLogin + ')');
                            _this.memberList = dateLogin
                        }

                    },
                    error: function (XMLHttpRequest, textStatus, errorThrown) {
                        console.log("获取工号失败！");
                    }
                });
            }

        },
        //确认点击事件
        handlebtnclick(){
            //console.log(this.radvalue);
            let nameObj = {};
            nameObj = this.radvalue;
            //console.log(nameObj.length)
			var Cache = localStorage.getItem('Cache');
			if(Cache == 'applyName'){
                if(nameObj.length > 1){
                    this.showPopup();
                    return
                }else{
                    localStorage.setItem('applyName',JSON.stringify(nameObj));
                }
			}else if(Cache == 'texture'){
                if(nameObj.length > 1){
                    this.showPopup();
                    return
                }else{
                    localStorage.setItem('texture',JSON.stringify(nameObj));
                }
            }else if(Cache == 'direct'){
                if(nameObj.length > 1){
                    this.showPopup();
                    return
                }else{
                    localStorage.setItem('direct',JSON.stringify(nameObj));
                }
            }else if(Cache == 'jilu'){
                if(nameObj.length > 1){
                    this.showPopup();
                    return
                }else{
                    localStorage.setItem('jilu',JSON.stringify(nameObj));
                }
            }else if(Cache == 'shenKe'){
                if(nameObj.length > 1){
                    this.showPopup();
                    return
                }else{
                    localStorage.setItem('shenKe',JSON.stringify(nameObj));
                }
            }else if(Cache == 'renyuan'){
                localStorage.setItem('renyuan',JSON.stringify(nameObj));
            }else if(Cache == null){
                this.showPopup();
                return
            };
            // var list5 = JSON.stringify(localStorage.getItem("Personnel"));
            // console.log(list5)
            //window.close()
			window.location.href = '/seeyon/m3/apps/v5/meetings/html/meeting_apply.html'
        },

        initpage() {

            // $('input[type="checkbox"]').click(function() {
            //     $(this).siblings('input[type="checkbox"]').prop('checked', false);
            //     alert(123)
            // });

             var token = this.getToken("rest", "123456");
             //根据loginname获取人员信息
             // var code = "A000543"
             // var member = this.getUserByCode(code,token)
             //console.log(member)
             //var orgAccountName = member[0].orgAccountName
             //console.log(orgAccountName)
             //获取部门下所有人员
             this.memberList = this.getUserListByDepartmentId("-2936870727963884205",token)
             console.log(this.memberList)
             //获取部门子部门
             //var subDepartment = new Array()
             //this.subDepartment=this.getSubDepartmentByParent("-2936870727963884205",token)
             //console.log(subDepartment)
             //获取部门父部门
             //var parentDepartment = this.getParentDepartmentByChild("-2936870727963884205",token)
             //console.log(parentDepartment)

		},
		getToken(username, password) {

            var url = "http://10.1.19.170";
            var token = null;

            $.ajax({
                url: (url + "/seeyon/rest/token/"+username+"/"+password),
                type: "get",
                dataType: "json",
                async: false,
                //data: {"userName":"' + username + '", "password":"' + password + '"},
                processData: false,
                headers: {"Accept": "application/json", "Content-Type": "application/json"},
                success: function (data) {
                    token = data.id;
                    console.log("getToken:", data)
                },
                error: function (XMLHttpRequest, textStatus, errorThrown) {
                    console.log("获取 TOKEN 失败！");
                }
            });


            return token;
        },
        getUserByCode(code,tk){
            var url = "http://10.1.19.170";
            var member = {}
            var header={
                "Accept": "application/json; charset=utf-8",
                "Accept-Language": "zh-CN",
                "Content-Type": "application/json; charset=utf-8"
                //"token": tk
            }

            $.ajax({
                url: url + "/seeyon/rest/orgMember/code/"+code+"?token="+tk,
                type: "get",
                dataType: "json",
                async: false,
                headers: header,
                processData: false,
                success: function (data) {
                    member = data
                },
                error: function (XMLHttpRequest, textStatus, errorThrown) {
                    console.log("获取 TOKEN 失败！");
                }
            });
            return member
        },
        getUserListByDepartmentId(departmentId,tk){
            var url = "http://10.1.19.170";
            var memberList = new Array()
            var header={
                "Accept": "application/json; charset=utf-8",
                "Accept-Language": "zh-CN",
                "Content-Type": "application/json; charset=utf-8"
                //"token": tk
            }

            $.ajax({
                url: url + "/seeyon/rest/orgMembers/department/"+departmentId+"?token="+tk,
                type: "get",
                dataType: "text",
                async: false,
                headers: header,
                processData: false,
                success: function (data) {
                    var orgLogin = data.replace(/(\".*?\"\s*\:\s*)(\-{0,1}\d+)/g, '$1' + '"$2"');
                    var dateLogin = eval('(' + orgLogin + ')');
                    memberList = dateLogin
                },
                error: function (XMLHttpRequest, textStatus, errorThrown) {
                    console.log("获取 TOKEN 失败！");
                }
            });
            return memberList
        },
        getSubDepartmentByParent(departmentId,tk){
            var url = "http://10.1.19.170";
            var memberList = new Array()
            var header={
                "Accept": "application/json; charset=utf-8",
                "Accept-Language": "zh-CN",
                "Content-Type": "application/json; charset=utf-8"
                //"token": tk
            }

            $.ajax({
                url: url + "/seeyon/rest/orgDepartments/children/"+departmentId+"?token="+tk,
                type: "get",
                dataType: "json",
                async: false,
                headers: header,
                processData: false,
                success: function (data) {
                    memberList = data
                },
                error: function (XMLHttpRequest, textStatus, errorThrown) {
                    console.log("获取 TOKEN 失败！");
                }
            });
            return memberList
        },
        getParentDepartmentByChild(departmentId,tk){
            var url = "http://10.1.19.170";
            var memberList = new Array()
            var header={
                "Accept": "application/json; charset=utf-8",
                "Accept-Language": "zh-CN",
                "Content-Type": "application/json; charset=utf-8"
                //"token": tk
            }

            $.ajax({
                url: url + "/seeyon/rest/orgDepartments/parent/"+departmentId+"?token="+tk,
                type: "get",
                dataType: "json",
                async: false,
                headers: header,
                processData: false,
                success: function (data) {
                    memberList = data
                },
                error: function (XMLHttpRequest, textStatus, errorThrown) {
                    console.log("获取 TOKEN 失败！");
                }
            });
            return memberList
        }
    },

}



</script>

