                	<div class="selectOrg"><i class="iconfont icon-xiaji"></i>下级</div>
                </li>
                <li class="homebinded">
                	<div class="group">环保集团</div>
                	<div class="selectOrg"><i class="iconfont icon-xiaji"></i>下级</div>
                </li>
                <li class="homebinded">
                	<div class="group">教育集团</div>
                	<div class="selectOrg"><i class="iconfont icon-xiaji"></i>下级</div>
                </li>
                <li class="homebinded">
                	<div class="group">商业集团</div>
                	<div class="selectOrg"><i class="iconfont icon-xiaji"></i>下级</div>
                </li>
