    	top: 0.5rem;
    	left: 50%;
    	width: 10rem;
    	margin-left: -5rem;
        background: #FFFFFF;
    	text-align: center;
    	line-height: 1.875rem;
    	height: 2.125rem;
    	box-sizing: border-box;
    	padding: 0.25rem 0.75rem 0.25rem 0.75rem;
    	border:1px solid #3aadfb;
    	border-color: #3aadfb;
    	border-radius: 2.125rem;
    	font-size: 1rem;
    	color: #3aadfb;
