import Vue from 'vue'
import Router from 'vue-router'
import Common from '@/pages/common/Common.vue'
import Danwei from '@/pages/danwei/Danwei.vue'
import Jituan from '@/pages/jituan/Jituan.vue'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Common',
      component: Common
    },{
      path: '/danwei',
      name: 'Danwei',
      component: Danwei
    },{
      path: '/jituan',
      name: 'Jituan',
      component: Jituan
    }
  ]
})



