<template>
    <div class="container">
        <div class="popup">
          <header class="popup-header">
            <slot name="header">
              提示
            </slot>
          </header>
          <section class="popup-body">
            <slot name="body">
              人员只能选择一个
            </slot>
          </section>
          <footer class="popup-footer">
            <slot name="footer">
              <button
              type="button"
              class="btn-green"
              @click="close">确定
              </button>
            </slot>
          </footer>
        </div>
      </div>
</template>

<script>
export default {
  name: 'popup',
  methods:{
    close (){
      this.$emit('close')
    }
  }

}
</script>

<style>
    .container {
        position: fixed;
        top: 0;
        bottom: 0;
        left: 0;
        right: 0;
        display: flex;
        justify-content: center;
        align-items: center;
      }
      .popup {
        width:50%;
        background: #FFFFFF;
        box-shadow: 2px 2px 20px 1px;
        overflow-x: auto;
        display: flex;
        flex-direction: column;
      }
      .popup-header{
        padding: 15px;
        color: #000000 !important;
        text-align: center;
      }
      .popup-footer{
        padding: 15px;
        display: flex;
        justify-content: flex-end;
        font-size: 1rem;
      }
      .popup-header {
        color: #4AAE9B;
        justify-content: space-between;
        font-size: 1rem;
      }
      .popup-body {
        position: relative;
        text-align: center;
        padding: 20px 10px;
        font-size: 0.9rem;
      }
      .btn-green {
        color: white;
        font-size: 1rem;
        background: #3aadfb;
        border-radius: 2px;
      }
</style>



